import { mapState, mapMutations } from 'vuex';
import {getOrderDetail} from '@/api/device';
export default {
	data() {
		return {
			type:0,
			price:0,   
			back:null
		}
	},
	computed:{
		...mapState(["currentService"])
	},
	onLoad(e) {
		this.type = e.type;
		uni.setNavigationBarTitle({
		　　title:'服务订阅'
		})
		getOrderDetail(e.orderId).then(res=>{
			if(res.data.data){
				this.$store.commit("setCurrentService", res.data.data)
			}
		})
	},
	mounted(){
		if(this.type == 1){
			this.back = null
		} else {
			this.back = './device'
		}
	},
	methods: {
		...mapMutations(["setSelectDevice", "setCurrentService"]),
		selectService(e){
			let service = this.services.find(x=>x.type == e.detail.value);
			if(service){
				this.$store.commit("setCurrentService", service)
			}
			
		},
		goToServiceDetail(){
			uni.navigateTo({
				url:'./serviceDetail'
			})
		},
		goToDevices(){
			// if(this.type == 1){
			// 	this.back = null
			// 	uni.navigateBack({
					
			// 	})
